<template>

		<section>
			<!--工具条-->
			<el-col :span="24" class="toolbar tool">
				<el-form :inline="true">
					<el-form-item>
						<el-button type="warning" v-on:click="addRoleForm.visible = true">添加</el-button>
					</el-form-item>
				</el-form>
			</el-col>

			<!--列表-->
			<el-table :data="tableData" highlight-current-row border>
				<el-table-column prop="roleName" label="角色名">
				</el-table-column>
				<el-table-column prop="code" label="编码">
				</el-table-column>
				<el-table-column prop="createDate" label="录入时间">
				</el-table-column>
				<el-table-column label="操作" width="150">
					<template scope="scope">
						<el-button type="text" size="small" @click="openAuthorizationDialogFun(scope.row)">授权</el-button>
						<el-button type="text" size="small" @click="openUpdateRoleDialogFun(scope.row)">修改</el-button>
					</template>
				</el-table-column>
			</el-table>
			<!--工具条-->
			<el-col :span="24" class="toolbar tool">
				<el-pagination @size-change="sizeChangeFun" @current-change="currentChangeFun" :current-page="pagination.currentPage" :page-sizes="[10, 20, 30, 50]" :page-size="pagination.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="pagination.total" style="float:right;">
				</el-pagination>
			</el-col>

			<!--添加用户界面-->
			<el-dialog title="添加角色" :visible.sync="addRoleForm.visible" size="tiny">
			  <el-form label-width="80px">
			  	<el-form-item label="角色名">
			      <el-input v-model="addRoleForm.roleName"></el-input>
			    </el-form-item>
			    <el-form-item label="编码">
			      <el-input v-model="addRoleForm.code"></el-input>
			    </el-form-item>
			  </el-form>
			  <div slot="footer" class="dialog-footer">
			    <el-button @click="addFunctionRoleDialog.visible = false">取 消</el-button>
			    <el-button type="primary" @click="addRoleFun">确 定</el-button>
			  </div>
			</el-dialog>

			<!--修改用户界面-->
			<el-dialog title="修改角色" :visible.sync="updateRoleForm.visible" size="tiny">
			  <el-form label-width="80px">
			  	<el-form-item label="角色名">
			      <el-input v-model="updateRoleForm.roleName"></el-input>
			    </el-form-item>
			    <el-form-item label="编码">
			      <el-input v-model="updateRoleForm.code"></el-input>
			    </el-form-item>
			  </el-form>
			  <div slot="footer" class="dialog-footer">
			    <el-button @click="updateRoleForm.visible = false">取 消</el-button>
			    <el-button type="primary" @click="updateRoleFun">确 定</el-button>
			  </div>
			</el-dialog>

			<!--授权界面-->
			<el-dialog title="授权界面" :visible.sync="authorizationForm.visible" size="tiny">
				<div style="height: 400px; overflow-y: auto">
					<el-tree
					  :data="tree.data"
					  node-key="id"
					  show-checkbox
					  default-expand-all
					  :props="tree.defaultProps"
					  ref="tree"
					  :check-strictly="true"

					  accordion>
					</el-tree>
			    </div>
			    <div slot="footer" class="dialog-footer">
				    <el-button @click="authorizationForm.visible = false">取 消</el-button>
				    <el-button type="primary" @click="addAuthorizationFun">确 定</el-button>
			    </div>
			</el-dialog>

		</section>

</template>

<script>
import urls from '../../common/js/urls'
import http from '../../common/js/http'
import util from '../../common/js/util'

export default {
	data() {
		return {
			tableData: [],
			//分页
			pagination:{
				currentPage: 1,
				total: 0,
				pageSize: 20
			},
			addRoleForm:{
				visible: false,
				roleName: "",
				code: ""
			},
			updateRoleForm:{
				visible: false,
				id: "",
				roleName: "",
				code: ""
			},
			authorizationForm:{
				visible: false,
				roleId: "",
				authorityCode: ""
			},
			tree:{
				visible: false,
				data: [],
				ids: [],
				defaultProps: {
		          children: 'child',
		          label: 'menuName'
		        },
			}
		}
	},
	methods: {
		//改变显示条数
		sizeChangeFun: function(val){
			this.pagination.pageSize = val;
			this.listRoleFun();
		},
		//改变当前页码
		currentChangeFun: function(val){
			this.pagination.currentPage = val;
			this.listRoleFun();
		},
		//获取功能角色列表
		listRoleFun: function () {
			http.post(this, urls.listRole, {page:this.pagination.currentPage, size: this.pagination.pageSize}, function (result) {
				this.pagination.total = result.data.total;
				this.tableData = result.data.data;
			}.bind(this))
		},
		//添加功能角色
		addRoleFun:function(){
			let data = {roleName: this.addRoleForm.roleName, code: this.addRoleForm.code};
			http.post(this, urls.addRole, data, function(result){
				util.toast(this, result.msg);
				this.listRoleFun();
				this.addRoleForm.visible = false;
				this.addRoleForm.roleName = "";
				this.addRoleForm.code = "";
			}.bind(this));
		},
		//打开修改角色对话框
		openUpdateRoleDialogFun:function(row){
			this.updateRoleForm.id = row.id;
			this.updateRoleForm.roleName = row.roleName;
			this.updateRoleForm.code = row.code;
			this.updateRoleForm.visible = true;
		},
		//修改功能角色
		updateRoleFun:function(){
			let data = {id: this.updateRoleForm.id, roleName: this.updateRoleForm.roleName, code: this.updateRoleForm.code};
			http.post(this, urls.updateRole, data, function(result){
				this.listRoleFun();
				util.toast(this, result.msg);
				this.updateRoleForm.visible = false;
			}.bind(this));
		},
		//打开授权界面
		openAuthorizationDialogFun:function(row){
			this.authorizationForm.authorityCode = row.authorityCode;
			this.authorizationForm.roleId = row.id;
			this.authorizationForm.visible = true;
			this.loadNodeFun();
		},
		//菜单列表
		loadNodeFun: function(){
			this.tree.ids = [];
			http.post(this, urls.cascadeMenuRole +"/"+ this.authorizationForm.roleId, {}, function(result){
				this.tree.data = result.data.menu;
				this.$refs.tree.setCheckedKeys(result.data.check);
			}.bind(this));
		},
		//还原角色拥有的权限
		reductionAuthorizationFun: function(id){
			let authorityCode = this.authorizationForm.authorityCode;
			if (authorityCode <= 0) {
				return;
			}
			if ((Math.pow(2, id) & authorityCode) == Math.pow(2, id)) {
				this.tree.ids.push(id);
			}
		},
		//赋予角色权限
		addAuthorizationFun: function(){
			let keys = this.$refs.tree.getCheckedKeys();
			let data = {id: this.authorizationForm.roleId, authorityCode: keys.toString()};
			http.post(this, urls.authorized, data, function(result){
				util.toast(this, result.msg);
				this.authorizationForm.visible = false;
				this.listRoleFun();
			}.bind(this));
		}
	},
	mounted() {
		this.listRoleFun();
	}
}

</script>

<style type="text/css">
	.tool{
		background-color: #fff;
		border: 1px solid #d1dbe5;
		border-radius: 5px;
	}

	.toolbar .el-form-item{
		margin-bottom: 0px;
	}
</style>


