<template>
	<div>

    <el-button style="margin-bottom: 8px" type="primary" @click="openAddDialogFun('', '', '-1')">添加菜单</el-button>

		<el-tree
		  :data="treeData"
		  node-key="id"
      default-expand-all
		  :props="defaultProps"
      :expand-on-click-node="false"
		  :render-content="renderContent">
		</el-tree>

		<!--添加页面功能dialog-->
		<el-dialog title="添加菜单" :visible.sync="addForm.visible" size="tiny">
		  <el-form label-width="80px">
		    <el-form-item label="菜单名称">
		      <el-input v-model="addForm.menuName"></el-input>
		    </el-form-item>
		    <el-form-item label="菜单编码">
		      <el-input v-model="addForm.menuCode"></el-input>
		    </el-form-item>
		  </el-form>
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="addForm.visible = false">取 消</el-button>
		    <el-button type="primary" @click="addMenuFun">确 定</el-button>
		  </div>
		</el-dialog>

    <!--修改页面功能dialog-->
    <el-dialog title="修改菜单" :visible.sync="updateForm.visible" size="tiny">
      <el-form label-width="80px">
        <el-form-item label="菜单名称">
          <el-input v-model="updateForm.menuName"></el-input>
        </el-form-item>
        <el-form-item label="菜单编码">
          <el-input v-model="updateForm.menuCode"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="updateForm.visible = false">取 消</el-button>
        <el-button type="primary" @click="updateMenuFun">确 定</el-button>
      </div>
    </el-dialog>

	</div>
</template>

<script>

	import urls from '../../common/js/urls'
	import http from '../../common/js/http'
	import util from '../../common/js/util'

  export default {
    data() {
      return {
        treeData: [],
        defaultProps: {
          children: 'child',
          label: 'menuName'
        },
        addForm: {
          visible: false,
          parentId: "",
          menuName: "",
          menuCode: "",
        },
        updateForm: {
          visible: false,
          id: "",
          menuName: "",
          menuCode: "",
        }
      }
    },

    methods: {
      loadNodeFun: function(){
      	http.post(this, urls.cascadeMenu, {}, function(result){
          this.treeData = result.data;
        }.bind(this));
      },
      openAddDialogFun: function(store, data, id) {
        if(id == -1){
          this.addForm.parentId = id;
        }else{
          this.addForm.parentId = data.id;
        }
        this.addForm.menuName = "";
        this.addForm.menuCode = "";
      	this.addForm.visible = true;
      },
      openUpdateMenuFun: function(store, data, id) {
        this.updateForm.id = data.id;
        this.updateForm.menuName = data.menuName;
        this.updateForm.menuCode = data.menuCode;
        this.updateForm.visible = true;
      },
      addMenuFun: function() {
        let data = {parentId: this.addForm.parentId, menuName: this.addForm.menuName, menuCode: this.addForm.menuCode};
      	http.post(this, urls.addMenu, data, function (result) {
          this.addForm.visible = false;
          util.toast(this, result.msg);
          this.loadNodeFun();
        }.bind(this))
      },
      deleteMenuFun: function(store, data) {
        util.confirm(this, function(){
          http.post(this, urls.delMenu+"/"+data.id, data, function (result) {
            util.toast(this, result.msg);
            this.loadNodeFun();
          }.bind(this))
        }.bind(this));
      },
      updateMenuFun: function() {
        let data = {id: this.updateForm.id, menuName: this.updateForm.menuName, menuCode: this.updateForm.menuCode};
        http.post(this, urls.updateMenu, data, function (result) {
          this.updateForm.visible = false;
          util.toast(this, result.msg);
          this.loadNodeFun();
        }.bind(this))
      },
      renderContent(h, { node, data, store }) {
      	return (
          <span>
            <span>
              <span>{node.label}</span>
            </span>
            <span style="float: right; margin-right: 20px">
              <el-button size="mini" on-click={ () => this.openAddDialogFun(store, data) }>添加子菜单</el-button>
              <el-button size="mini" on-click={ () => this.deleteMenuFun(store, data) }>删除</el-button>
              <el-button size="mini" on-click={ () => this.openUpdateMenuFun(store, data) }>修改</el-button>
            </span>
          </span>
        );
      }
    },
    mounted: function(){
      this.loadNodeFun();
    }
  };
</script>


