<template>

		<section>
			<!--工具条-->
			<el-col class="toolbar tool" :span="24">
				<el-form :inline="true" :model="filters">
					<el-form-item>
						<el-input v-model="filters.name" placeholder="姓名"></el-input>
					</el-form-item>
					<el-form-item>
						<el-button type="primary" v-on:click="listUserFun">查询</el-button>
					</el-form-item>
					<el-form-item>
						<el-button type="warning" v-on:click="openAddUserDialogFun">添加</el-button>
					</el-form-item>
				</el-form>
			</el-col>

			<!--列表-->
			<el-table :data="tableData" highlight-current-row style="width: 100%;" border>
				<el-table-column prop="realName" label="姓名">
				</el-table-column>
				<el-table-column prop="account" label="帐号">
				</el-table-column>
				<el-table-column prop="createDate" label="录入时间">
				</el-table-column>
				<el-table-column label="操作" width="150">
					<template scope="scope">
						<el-button type="text" size="small" @click="authorizationDialogFun(scope.row)">授权</el-button>
						<el-button type="text" size="small" @click="openUpdateUserDialogFun(scope.row)">修改</el-button>
						<el-button type="text" size="small" @click="delUserFun(scope.row)">删除</el-button>
					</template>
				</el-table-column>
			</el-table>
			<!--工具条-->
			<el-col :span="24" class="toolbar tool">
					<el-pagination @size-change="sizeChangeFun" @current-change="currentChangeFun" :current-page="pagination.currentPage" :page-sizes="[10, 20, 30, 50]" :page-size="pagination.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="pagination.total" style="float:right;">
				</el-pagination>
			</el-col>

			<!--添加用户界面-->
			<el-dialog title="添加用户" :visible.sync="addUserForm.visible" size="tiny">
			  <el-form label-width="50px">
			  	<el-form-item label="姓名">
			      <el-input v-model="addUserForm.realName"></el-input>
			    </el-form-item>
			    <el-form-item label="帐号">
			      <el-input v-model="addUserForm.account"></el-input>
			    </el-form-item>
			  </el-form>
			  <div slot="footer" class="dialog-footer">
			    <el-button @click="addUserForm.visible = false">取 消</el-button>
			    <el-button type="primary" @click="addUserFun">确 定</el-button>
			  </div>
			</el-dialog>

			<!--添加用户界面-->
			<el-dialog title="修改用户" :visible.sync="updateUserForm.visible" size="tiny">
			  <el-form label-width="50px">
			  	<el-form-item label="姓名">
			      <el-input v-model="updateUserForm.realName"></el-input>
			    </el-form-item>
			    <el-form-item label="帐号">
			      <el-input v-model="updateUserForm.account" disabled></el-input>
			    </el-form-item>
			  </el-form>
			  <div slot="footer" class="dialog-footer">
			    <el-button @click="updateUserForm.visible = false">取 消</el-button>
			    <el-button type="primary" @click="updateUserFun">确 定</el-button>
			  </div>
			</el-dialog>

			<!--授权界面-->
			<el-dialog title="角色授权" :visible.sync="authorizationForm.visible" size="tiny">
			  <el-radio-group v-model="authorizationForm.checkRoleId">
			  	<el-row v-for="roles in authorizationForm.allRoles" :gutter="100" style="margin-bottom: 8px">
				    <el-col :span="6" v-for="role in roles"><el-radio :label="role.id">{{role.roleName}}</el-radio></el-col>
			  	</el-row>
			  </el-radio-group>

			  <div slot="footer" class="dialog-footer">
			    <el-button @click="authorizationForm.visible = false">取 消</el-button>
			    <el-button type="primary" @click="authorizedUserFun">确 定</el-button>
			  </div>
			</el-dialog>

		</section>
</template>

<script>
import urls from '../../common/js/urls'
import http from '../../common/js/http'
import util from '../../common/js/util'

export default {
	data() {
		return {
			tableData: [],
			//分页
			pagination:{
				currentPage: 1,
				total: 0,
				pageSize: 20
			},
			filters: {
				name: ''
			},
			addUserForm:{
				visible: false,
				account: "",
				realName: "",
			},
			updateUserForm:{
				visible: false,
				id: "",
				account: "",
				realName: "",
			},
			authorizationForm:{
				visible: false,
				allRoles:[],
				userId: "",
				checkRoleId: ""
			}
		}
	},
	computed:{
	},
	methods: {
		//改变显示条数
		sizeChangeFun: function(val){
			this.pagination.pageSize = val;
			this.listUserFun();
		},
		//改变当前页码
		currentChangeFun: function(val){
			this.pagination.currentPage = val;
			this.listUserFun();
		},
		//获取用户列表
		listUserFun: function () {
			http.post(this, urls.listUser, {page:this.pagination.currentPage, size: this.pagination.pageSize}, function (data) {
				this.pagination.total = data.data.total;
				this.tableData = data.data.data;
			}.bind(this))
		},
		//打开添加用户对话框
		openAddUserDialogFun: function(){
			this.addUserForm.realName = "";
			this.addUserForm.account = "";
			this.addUserForm.visible = true;
		},
		//添加用户
		addUserFun:function(){
			let data = {account: this.addUserForm.account, realName: this.addUserForm.realName};
			http.post(this, urls.addUser, data, function(result){
				util.toast(this, result.msg);
				this.addUserForm.visible = false;
				this.listUserFun();
			}.bind(this));
		},
		//打开修改用户对话框
		openUpdateUserDialogFun: function(row){
			this.updateUserForm.id = row.id;
			this.updateUserForm.realName = row.realName;
			this.updateUserForm.account = row.account;
			this.updateUserForm.visible = true;
		},
		//修改用户
		updateUserFun:function(){
			let data = {id: this.updateUserForm.id, account: this.updateUserForm.account, realName: this.updateUserForm.realName};
			http.post(this, urls.updateUser, data, function(result){
				util.toast(this, result.msg);
				this.updateUserForm.visible = false;
				this.listUserFun();
			}.bind(this));
		},
		//打开授权界面
		authorizationDialogFun: function(row){
			this.authorizationForm.checkRoleId = row.roleId;
			this.authorizationForm.userId = row.id;
			this.authorizationForm.visible = true;
			this.listRoleFun();
		},
		//获取功能角色列表
		listRoleFun: function () {
			this.authorizationForm.allRoles = [];
			http.post(this, urls.listRole, {page:1, size: 1000}, function (result) {
				let group = 4;
				let datas = result.data.data;
				let len = datas.length % group > 0 ? Math.floor(datas.length / group) + 1 : datas.length / group;
				for (var i = 0; i < len; i++) {
					this.authorizationForm.allRoles.push(datas.slice(i * group, i * group + group));
				}
			}.bind(this))
		},
		//授权
		authorizedUserFun: function () {
			let data = {id: this.authorizationForm.userId, roleId: this.authorizationForm.checkRoleId};
			http.post(this, urls.authorizedUser, data, function (result) {
				util.toast(this, result.msg);
				this.authorizationForm.visible = false;
				this.listUserFun();
			}.bind(this))
		},
		//删除用户
		delUserFun: function (row) {
			util.confirm(this, function(){
				http.post(this, urls.delUser+"/"+row.account, {}, function (result) {
					util.toast(this, result.msg);
					this.listUserFun();
				}.bind(this))
			}.bind(this))
		},
	},
	mounted() {
		this.listUserFun();
	}
}

</script>

<style type="text/css">
	.tool{
		background-color: #fff;
		border: 1px solid #d1dbe5;
		border-radius: 5px;
	}

	.toolbar .el-form-item{
		margin-bottom: 0px;
	}
</style>



