import axios from 'axios';
import qs from 'qs';

var host = "http://47.90.56.155:8023";
// var host = "http://127.0.0.1:8023";
// var host = "http://192.168.1.154:8023";
axios.defaults.withCredentials=true;

export default {
    post :function(vueObj, url, data = {}, cab){
        axios.post(host + url, qs.stringify(data)).then(res => {
            let data = res.data;
            if(data.code == 1){
                cab(data);
            }else if(data.code == 10007){
                vueObj.$router.push('/login');
            }else{
                vueObj.$notify.info({
                  title: '系统提示',
                  message: data.msg
                });
            }
        })
    }
}


