<template>
  <el-form label-position="left" label-width="0px" class="demo-ruleForm login-container">
    <h3 class="title">系统登录</h3>
    <el-form-item prop="account">
      <el-input type="text" v-model="username" auto-complete="off" placeholder="账号"></el-input>
    </el-form-item>
    <el-form-item prop="checkPass">
      <el-input type="password" v-model="password" auto-complete="off" placeholder="密码" @keyup.enter.native="login"></el-input>
    </el-form-item>
    <el-form-item style="width:100%;">
      <el-button type="primary" style="width:100%;" @click.native.prevent="login" :loading="logining" @keyup.enter.native="login">登录</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
  import http from '../common/js/http';
  import md5 from '../common/js/md5';

  export default {
    data() {
      return {
        logining: false,
        username: '',
        password: '',
        checked: true
      };
    },
    methods: {
      login:function() {
        var _this = this;
        this.logining = true;
        http.post(this, '/user/login', { account: this.username, password: md5.hex_md5(this.password) }, function(data){
            sessionStorage.setItem('user', JSON.stringify(data.data));
            _this.$router.push('/userList');
        });
        this.logining = false;
      }
    }
  }
</script>

<style lang="scss" scoped>
  .login-container {
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
    .title {
      margin: 0px auto 40px auto;
      text-align: center;
      color: #505458;
    }
    .remember {
      margin: 0px 0px 35px 0px;
    }
  }
</style>


